/**
 * ç”Ÿæˆéšæœºåˆå§‹è½¬ç”Ÿç‚¹æ•°
 * èŒƒå›´: 100-1000
 * æ¦‚ç‡åˆ†å¸ƒ: é™ä½300ä»¥ä¸Šçš„æ¦‚ç‡
 * ä½¿ç”¨åŠ æƒéšæœºï¼Œå€¾å‘äºç”Ÿæˆè¾ƒä½çš„ç‚¹æ•°
 */
export const generateInitialPoints = (): number => {
  const random = Math.random();

  // 70% æ¦‚ç‡: 100-300 ç‚¹
  if (random < 0.7) {
    return Math.floor(Math.random() * 201) + 100; // 100-300
  }
  // 20% æ¦‚ç‡: 300-500 ç‚¹
  else if (random < 0.9) {
    return Math.floor(Math.random() * 201) + 300; // 300-500
  }
  // 10% æ¦‚ç‡: 500-1000 ç‚¹
  else {
    return Math.floor(Math.random() * 501) + 500; // 500-1000
  }
};

